import {
    Button,
    Container,
    Image, 
    Dimmer,
    Segment
} from 'semantic-ui-react'; 
import React from 'react'


 class PublicDomainReader extends React.Component {
     state = {
         activeImage:'', 
         dimmerActive: false
     }
    
    toggleDimmer = () =>{
        this.setState({dimmerActive: !this.state.dimmerActive})
    }
    render(){
        return(
            <div>
                <Image onClick={() => this.toggleDimmer()} src={'/static/pdr-gif-1.gif'} />
                <Dimmer active={this.state.dimmerActive} onClickOutside={this.handleHide}>
                    <Container>
                        <Image src={this.state.activeImage} size="massive" verticalAlign="top"/>
                    </Container>
                </Dimmer>
            </div>
        )
    }
}

export default PublicDomainReader


